/** @format */

.card {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  flex-wrap: wrap;
  border: 1px solid #999;
  cursor: pointer;
}

.img {
  width: 100%;
}

.card-text {
  display: flex;
  flex-direction: column;
  padding: 10px;
  font-size: 20px;
  gap: 5px;
}
